.apology-section {
	position: relative;
	background: var(--color-primary);
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.appology-wrap {
		position: relative;
		padding: 5rem 2rem 3rem;
		border-radius: 500px 500px 20px 20px;
		overflow: hidden;
		@include max-md {
			padding: 4rem 1rem 2rem;
		}
	}
}
