.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 3/4;
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 500px;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 2rem;
		overflow: hidden;
		@include max-md {
			padding: 2rem 1rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 1);
				border: 1px solid var(--color-secondary);
				border-radius: 3px;
			}
		}
	}
}
