.reminder-wrap {
	position: relative;
	display: block;
	padding: 2rem 0 5rem;
	@include max-md {
		padding: 2rem 0;
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		padding: 8rem 5rem;
		border-radius: 500px;
		overflow: hidden;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
