.venue-section {
	position: relative;
	padding-top: 12rem;
	@include max-lg {
		padding-top: 8rem;
	}
	@include max-md {
		padding-top: 5rem;
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		padding: 8rem 1rem 5rem;
		background: var(--color-primary);
		border-radius: 1000px 1000px 20px 20px;
		@include max-md {
			padding: 5rem 1rem 3rem;
		}
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				margin-top: 2rem;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
			}
			.card {
				background-color: var(--color-main);
				height: 100%;
				text-align: center;
				overflow: hidden;
				border: none;
				border-radius: 20px;
				box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
				mask-image: url(../images/mask-union.png);
				mask-size: 100%;
				mask-position: 50% 0%;
				mask-repeat: no-repeat;
				.card-body {
					position: relative;
					padding: 6rem 2rem 3rem;
					@include max-md {
						padding: 5rem 1rem 2rem;
					}
					h4 {
						margin: 1rem 0;
					}
					hr {
						display: block;
						width: 10%;
						height: 2px;
						margin: 0 auto 1rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		@include max-md {
			gap: 3rem;
		}
	}
	.event-name {
		position: relative;
		width: 100%;
		.date {
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			h5 {
				font-family: $font-secondary;
				font-weight: 400;
				margin-bottom: 0;
				font-size: 42px;
				min-width: 30%;
				@include max-md {
					font-size: 36px;
				}
			}
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
