.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			display: none;
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
	.couple-wrapper {
		position: relative;
		padding: 12rem 0 5rem;
		@include max-md {
			padding: 5rem 0 3rem;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
	}
	.couple {
		position: relative;
		text-align: center;
		border-radius: 500px 500px 20px 20px;
		box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		padding: 2rem;
		margin-bottom: 1rem;
		.elements-wrapper-absolute {
			border-radius: 500px 500px 20px 20px;
			overflow: hidden;
		}
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 500px 500px 20px 20px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 0;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			bottom: -3rem;
			left: 50%;
			transform: translate(-50%, 50%);
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
